import { Navigation } from "@/components/navigation"
import { Footer } from "@/components/footer"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Brain, Zap, Target, CheckCircle2, ArrowRight, Sparkles, Activity, Stethoscope, Users, BookOpen, Moon, Heart, Home, FileText, HeartPulse, Dumbbell, Wind } from "lucide-react"
import Link from "next/link"

export default function BioBrainStudioPage() {
  const features = [
    {
      icon: Brain,
      title: "Cognitive Enhancement",
      description: "Advanced protocols to optimize brain function, memory, and mental performance",
    },
    {
      icon: Zap,
      title: "Neuroplasticity Training",
      description: "Evidence-based techniques to strengthen neural pathways and cognitive resilience",
    },
    {
      icon: Target,
      title: "Peak Mental Performance",
      description: "Unlock your cognitive potential for enhanced focus, creativity, and decision-making",
    },
    {
      icon: Activity,
      title: "Brain Health Optimization",
      description: "Comprehensive approach to maintaining and improving long-term cognitive health",
    },
  ]

  const programComponents = [
    "Cognitive assessment and baseline testing",
    "Neuroplasticity training protocols",
    "Nutrition for brain health",
    "Sleep optimization for cognition",
    "Stress management and mental clarity",
    "Supplement optimization for brain function",
    "Mental performance tracking",
    "Personalized cognitive enhancement plan",
  ]

  const outcomes = [
    {
      phase: "Foundation",
      duration: "Weeks 1-4",
      results: [
        "Cognitive baseline established",
        "Initial performance improvements",
        "Enhanced focus and clarity",
        "Improved mental energy",
      ],
    },
    {
      phase: "Enhancement",
      duration: "Months 2-6",
      results: [
        "Significant cognitive improvements",
        "Enhanced memory and recall",
        "Better decision-making capabilities",
        "Increased creativity and problem-solving",
      ],
    },
    {
      phase: "Optimization",
      duration: "Months 6+",
      results: [
        "Peak cognitive performance",
        "Sustained mental clarity",
        "Long-term brain health maintenance",
        "Advanced cognitive capabilities",
      ],
    },
  ]

  return (
    <main className="min-h-screen">
      <Navigation />

      {/* Hero Section */}
      <section className="relative pt-32 pb-24 px-6 lg:px-8 overflow-hidden">
        {/* Background Image */}
        <div
          className="absolute inset-0 -z-20 bg-cover bg-center bg-no-repeat"
          style={{
            backgroundImage: "url('/hero2.png')",
          }}
        />

        {/* Light Overlay for readability */}
        <div className="absolute inset-0 -z-10 bg-white/80 backdrop-blur-sm" />

        <div className="container max-w-5xl relative z-0 ml-0 lg:ml-8 xl:ml-16">
          <div className="text-left">
            {/* Badge */}
            <div className="inline-flex items-center px-3 py-1.5 rounded-md bg-purple-100 text-gray-700 text-sm font-medium mb-6">
              BioBrain Studio Program
            </div>

            {/* Headline with gradient */}
            <h1 className="font-serif text-4xl sm:text-5xl md:text-6xl lg:text-7xl font-bold text-foreground mb-6 leading-tight">
              Your physical space for{" "}
              <span className="bg-gradient-to-r from-teal-500 via-blue-500 to-purple-500 bg-clip-text text-transparent">
                guided transformation
              </span>
            </h1>

            {/* Description */}
            <p className="text-lg sm:text-xl text-muted-foreground leading-relaxed max-w-2xl mb-8">
              An immersive offline facility where clients experience comprehensive diagnostics, personalized coaching, and hands-on interventions for accelerated health transformation.
              <br />
              Where science meets practice in a dedicated space for your wellness journey.
            </p>

            {/* CTA Buttons */}
            <div className="flex flex-col sm:flex-row items-start gap-4">
              <Link href="/assessment">
                <Button
                  size="lg"
                  className="bg-gradient-to-r from-purple-600 to-purple-700 hover:from-purple-700 hover:to-purple-800 text-white px-8 h-12 text-base rounded-lg"
                >
                  Start Cognitive Assessment
                </Button>
              </Link>
              <Link href="/contact">
                <Button
                  size="lg"
                  variant="outline"
                  className="bg-white border border-gray-300 text-gray-700 hover:bg-gray-50 px-8 h-12 text-base rounded-lg"
                >
                  Book Consultation
                </Button>
              </Link>
            </div>
          </div>
        </div>
      </section>

      {/* What is BioBrain Studio */}
      <section className="py-16 px-6 lg:px-8">
        <div className="container mx-auto max-w-4xl">
          <h2 className="font-serif text-3xl sm:text-4xl text-foreground mb-6 text-center">
            Unlock Your Cognitive Potential
          </h2>
          <div className="prose prose-lg max-w-none mb-12">
            <p className="text-muted-foreground leading-relaxed mb-4">
              BioBrain Studio is a comprehensive cognitive enhancement program designed to optimize brain function, improve mental performance, and support long-term brain health. Using the latest neuroscience research, we develop personalized protocols that enhance focus, memory, creativity, and decision-making.
            </p>
            <p className="text-muted-foreground leading-relaxed">
              Whether you're looking to boost your professional performance, enhance learning capabilities, or maintain cognitive health as you age, BioBrain Studio provides the tools and strategies to achieve your mental performance goals.
            </p>
          </div>

          <div className="grid grid-cols-1 sm:grid-cols-2 gap-6">
            {features.map((feature, index) => (
              <Card key={index} className="border-2">
                <CardHeader>
                  <div className="flex items-center gap-3">
                    <div className="w-10 h-10 rounded-lg bg-primary/10 flex items-center justify-center flex-shrink-0">
                      <feature.icon className="w-5 h-5 text-primary" />
                    </div>
                    <CardTitle className="font-serif text-lg">{feature.title}</CardTitle>
                  </div>
                </CardHeader>
                <CardContent>
                  <p className="text-sm text-muted-foreground">{feature.description}</p>
                </CardContent>
              </Card>
            ))}
          </div>
        </div>
      </section>

      {/* Program Components */}
      <section className="py-16 px-6 lg:px-8 bg-secondary/20">
        <div className="container mx-auto max-w-4xl">
          <div className="text-center mb-12">
            <h2 className="font-serif text-3xl sm:text-4xl text-foreground mb-4">Program Components</h2>
            <p className="text-lg text-muted-foreground max-w-2xl mx-auto">
              A comprehensive approach to cognitive enhancement
            </p>
          </div>

          <Card className="border-2">
            <CardContent className="pt-8">
              <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
                {programComponents.map((component, index) => (
                  <div key={index} className="flex items-start gap-3">
                    <CheckCircle2 className="w-5 h-5 text-primary flex-shrink-0 mt-0.5" />
                    <span className="text-muted-foreground">{component}</span>
                  </div>
                ))}
              </div>
            </CardContent>
          </Card>
        </div>
      </section>

      {/* Outcomes Timeline */}
      <section className="py-16 px-6 lg:px-8">
        <div className="container mx-auto max-w-6xl">
          <div className="text-center mb-12">
            <h2 className="font-serif text-3xl sm:text-4xl text-foreground mb-4">Expected Cognitive Improvements</h2>
            <p className="text-lg text-muted-foreground max-w-2xl mx-auto">
              Progressive enhancements as you optimize your cognitive function
            </p>
          </div>

          <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
            {outcomes.map((outcome, index) => (
              <Card key={index} className="border-2">
                <CardHeader>
                  <CardTitle className="font-serif text-2xl">{outcome.phase}</CardTitle>
                  <CardDescription className="text-accent font-medium">{outcome.duration}</CardDescription>
                </CardHeader>
                <CardContent>
                  <ul className="space-y-2">
                    {outcome.results.map((result, i) => (
                      <li key={i} className="flex items-start gap-2 text-sm text-muted-foreground">
                        <CheckCircle2 className="w-4 h-4 text-primary flex-shrink-0 mt-0.5" />
                        <span>{result}</span>
                      </li>
                    ))}
                  </ul>
                </CardContent>
              </Card>
            ))}
          </div>
        </div>
      </section>

      {/* CTA Section */}
      <section className="py-16 px-6 lg:px-8">
        <div className="container mx-auto max-w-3xl text-center">
          <h2 className="font-serif text-3xl sm:text-4xl text-foreground mb-6">Ready to Enhance Your Cognitive Performance?</h2>
          <p className="text-lg text-muted-foreground mb-8">
            Begin your journey to peak mental performance with our comprehensive cognitive assessment.
          </p>
          <div className="flex flex-col sm:flex-row items-center justify-center gap-4">
            <Link href="/assessment">
              <Button size="lg" className="px-8">
                Start Cognitive Assessment
                <ArrowRight className="ml-2 h-5 w-5" />
              </Button>
            </Link>
            <Link href="/contact">
              <Button size="lg" variant="outline" className="px-8 bg-transparent">
                Book Consultation
              </Button>
            </Link>
          </div>
          <p className="text-sm text-muted-foreground mt-6">Duration: 3-6 months | Investment: Custom pricing</p>
        </div>
      </section>

      <Footer />
    </main>
  )
}

